}
#search{
    background-image: url('https://i.ibb.co/vkFtQLg/food03.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 100% 500px;
    height: 500px;
    width: 119%;
    margin-top:-6%;
    margin-left:-15%;
}
#quicksearch{
    
    height:500px;
    width:100%; 
}
#login{
    margin-left: 75%;
}
#login> a{
    text-decoration:none;
    color:white;
    font-size: 30px;
    font-family:cursive;
    position:relative;
    bottom:-20px;
}
#create_account{
    border:2px solid white;
}
#logo{
    font-size:90px;
    color:rgb(6, 6, 87);
    text-align:center;
    background-color: white;
    height:150px;
    width:150px;
    margin:auto;
    border-radius: 50%;
}
#logo> span{
    font-weight:bold;
    position:absolute;
    margin-left:-37px;
    margin-top:18px;
}
